<template>
    <el-row>
        <el-col :span="6">
            <div class="grid-content bg-purple-dark">
                <el-input placeholder="请输入过滤条件" v-model="searchKey"></el-input>
            </div>
        </el-col>
        <el-col :span="24">
            <div id="gather-data-form" class="grid-content bg-purple-dark">
                <el-table :data="filteredList" width="100%" align="right" :stripe="true" header-align="right" @row-click="getDetail">
                    <el-table-column prop="name" label="实例"></el-table-column>
                </el-table>
            </div>
        </el-col>
        <el-col :span="24">
            <div class="grid-content bg-purple-dark">
                <el-dialog
                        title="详细信息"
                        :visible.sync="dialogVisible"
                        width="60%"
                        :before-close="handleClose">
                    <span>
                        <el-form ref="form" :model="form" label-width="100px">
                          <el-form-item label="APP">
                            <el-input v-model="form.app" :disabled="true"></el-input>
                          </el-form-item>
                          <el-form-item label="INSTANCE ID">
                            <el-input v-model="form.instanceId" :disabled="true"></el-input>
                          </el-form-item>
                          <el-form-item label="IP">
                            <el-input v-model="form.ip" :disabled="true"></el-input>
                          </el-form-item>
                          <el-form-item label="PORT">
                            <el-input v-model="form.port" :disabled="true"></el-input>
                          </el-form-item>
                          <el-form-item label="WEIGHT">
                            <el-input v-model="form.weight"></el-input>
                          </el-form-item>
                        </el-form>
                    </span>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="dialogVisible = false">取 消</el-button>
                        <el-button type="primary" @click="onSubmit">确 定</el-button>
                    </span>
                </el-dialog>
            </div>
        </el-col>
    </el-row>
</template>

<script>
    import Service from './service'
    export default {
      name: 'appManage',
      data () {
        return {
          searchKey: '',
          dataList: [],
          dialogVisible: false,
          formObj: {},
          form: {
            app: '',
            instanceId: '',
            ip: '',
            port: '',
            weight: ''
          }
        }
      },
      methods: {
        getDetail (val) {
          this.formObj = Object.assign({}, val.obj)
          this.form = val.obj
          this.dialogVisible = true
        },
        handleClose () {
          this.dialogVisible = false
        },
        onSubmit () {
          if (this.formObj.weight === this.form.weight) {
            this.dialogVisible = false
            return
          }
          if (parseInt(this.form.weight) > 10 || parseInt(this.form.weight) < 0) {
            this.$message({
              message: '[0,10]范围',
              type: 'warning'
            })
            return
          }
          Service.updateMeteData({app: this.form.app, instanceId: this.form.instanceId, weight: this.form.weight}).then(res => {
            this.dialogVisible = false
          })
        }
      },
      mounted () {
        Service.getData().then((data = {}) => {
          this.dataList = Object.keys(data.data).reduce((res, key, index) => {
            res.push({name: key, obj: data.data[key], index: index})
            return res
          }, [])
        }).catch(error => {
          console.log(error)
          this.$message.error('请求失败，请联系程序猿改BUG!')
        })
      },
      computed: {
        'filteredList' () {
          return this.dataList.filter(item => item.name.includes(this.searchKey)) || []
        }
      }
    }
</script>

<style scoped>

</style>


